'use strict';

/**
* WebAssembly routine to calculate the sum of absolute values (L1 norm) of double-precision floating-point strided array elements using pairwise summation.
*
* @module @stdlib/blas-ext-base-wasm-dasumpw
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import dasumpw from '@stdlib/blas-ext-base-wasm-dasumpw';
*
* // Define a strided array:
* var x = new Float64Array( [ 1.0, -2.0, 2.0 ] );
*
* // Perform operation:
* var v = dasumpw.main( x.length, x, 1 );
* // returns 5.0
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import dasumpw from '@stdlib/blas-ext-base-wasm-dasumpw';
*
* // Define a strided array:
* var x = new Float64Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
*
* // Perform operation:
* var v = dasumpw.ndarray( 4, x, 2, 1 );
* // returns 9.0
*
* @example
* import Memory from '@stdlib/wasm-memory';
* import oneTo from '@stdlib/array-one-to';
* import zeros from '@stdlib/array-zeros';
* import dasumpw from '@stdlib/blas-ext-base-wasm-dasumpw';
*
* // Create a new memory instance with an initial size of 10 pages (640KiB) and a maximum size of 100 pages (6.4MiB):
* var mem = new Memory({
*     'initial': 10,
*     'maximum': 100
* });
*
* // Create a BLAS routine:
* var mod = new dasumpw.Module( mem );
* // returns <Module>
*
* // Initialize the routine:
* mod.initializeSync();
*
* // Define a vector data type:
* var dtype = 'float64';
*
* // Specify a vector length:
* var N = 3;
*
* // Define a pointer (i.e., byte offset) for storing the input vector:
* var xptr = 0;
*
* // Write vector values to module memory:
* mod.write( xptr, oneTo( N, dtype ) );
*
* // Perform computation:
* var sum = mod.main( N, xptr, 1 );
* // returns 6.0
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import Module from './module.js';


// MAIN //

setReadOnly( main, 'Module', Module );


// EXPORTS //

export default main;

// exports: { "Module": "main.Module" }
