/* eslint-disable max-len, no-restricted-syntax, no-invalid-this */

'use strict';

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import inherits from '@stdlib/utils-inherit';
import stride2offset from '@stdlib/strided-base-stride2offset';
import Memory from '@stdlib/wasm-memory';
import arrays2ptrs from '@stdlib/wasm-base-arrays2ptrs';
import strided2object from '@stdlib/wasm-base-strided2object';
import Module from './module.js';


// MAIN //

/**
* Routine constructor.
*
* @private
* @constructor
* @returns {Routine} routine instance
*
* @example
* import Float64Array from '@stdlib/array-float64';
*
* // Create a new routine:
* var dasumpw = new Routine();
*
* // Initialize the module:
* dasumpw.initializeSync();
*
* // Define a strided array:
* var x = new Float64Array( [ 1.0, -2.0, 2.0 ] );
*
* // Perform operation:
* var sum = dasumpw.main( 3, x, 1 );
* // returns 5.0
*
* @example
* import Float64Array from '@stdlib/array-float64';
*
* // Create a new routine:
* var dasumpw = new Routine();
*
* // Initialize the module:
* dasumpw.initializeSync();
*
* // Define a strided array:
* var x = new Float64Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
*
* // Perform operation:
* var sum = dasumpw.ndarray( 4, x, 2, 1 );
* // returns 9.0
*/
function Routine() {
	if ( !( this instanceof Routine ) ) {
		return new Routine();
	}
	Module.call( this, new Memory({
		'initial': 0
	}));
	return this;
}

// Inherit from the parent constructor:
inherits( Routine, Module );

/**
* Computes the sum of absolute values (L1 norm) of double-precision floating-point strided array elements using pairwise summation.
*
* @name main
* @memberof Routine.prototype
* @readonly
* @type {Function}
* @param {PositiveInteger} N - number of indexed elements
* @param {Float64Array} x - input array
* @param {integer} strideX - stride length
* @returns {number} sum
*
* @example
* import Float64Array from '@stdlib/array-float64';
*
* // Create a new routine:
* var dasumpw = new Routine();
*
* // Initialize the module:
* dasumpw.initializeSync();
*
* // Define a strided array:
* var x = new Float64Array( [ 1.0, -2.0, 2.0 ] );
*
* // Perform operation:
* var sum = dasumpw.main( 3, x, 1 );
* // returns 5.0
*/
setReadOnly( Routine.prototype, 'main', function dasumpw( N, x, strideX ) {
	return this.ndarray( N, x, strideX, stride2offset( N, strideX ) );
});

/**
* Computes the sum of absolute values (L1 norm) of double-precision floating-point strided array elements using pairwise summation and alternative indexing semantics.
*
* @name ndarray
* @memberof Routine.prototype
* @readonly
* @type {Function}
* @param {PositiveInteger} N - number of indexed elements
* @param {Float64Array} x - input array
* @param {integer} strideX - stride length
* @param {NonNegativeInteger} offsetX - starting index
* @returns {number} sum
*
* @example
* import Float64Array from '@stdlib/array-float64';
*
* // Create a new routine:
* var dasumpw = new Routine();
*
* // Initialize the module:
* dasumpw.initializeSync();
*
* // Define a strided array:
* var x = new Float64Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
*
* // Perform operation:
* var sum = dasumpw.ndarray( 4, x, 2, 1 );
* // returns 9.0
*/
setReadOnly( Routine.prototype, 'ndarray', function dasumpw( N, x, strideX, offsetX ) {
	var ptrs;
	var p0;

	// Convert the input arrays to "pointers" in the module's memory:
	ptrs = arrays2ptrs( this, [
		strided2object( N, x, strideX, offsetX )
	]);
	p0 = ptrs[ 0 ];

	// Perform computation by calling the corresponding parent method:
	return Module.prototype.ndarray.call( this, N, p0.ptr, p0.stride, p0.offset );
});


// EXPORTS //

export default Routine;
